<template>
  <div class="row">
    <div>
      <b-button @click="show_popup_them_moi = true" variant="success" class="float-right"
        >Thêm mới hóa đơn</b-button
      >
      <b-modal v-model="show_popup_them_moi" title="Thêm mới hóa đơn" size="lg">
        <themmoi />
        <template #modal-footer>
          <div class="w-100">
            <b-button
              variant="primary"
              size="sm"
              class="float-right"
              @click="show_popup_them_moi = false"
            >
              Đóng
            </b-button>
          </div>
        </template>
      </b-modal>
      <b-modal v-model="show_popup_sua" title="Cập nhật hóa đơn" size="lg">
        <sua :hoadon_item="hoadon_item_selected" />
        <template #modal-footer>
          <div class="w-100">
            <b-button
              variant="primary"
              size="sm"
              class="float-right"
              @click="show_popup_sua = false"
            >
              Đóng
            </b-button>
          </div>
        </template>
      </b-modal>
    </div>
    <div class="col-md-12">
      <p class="h5 font-weight-bold text-center">Danh sách hóa đơn</p>
      <b-form-group label="Tìm kiếm" label-for="filter-input" class="my-2">
        <b-input-group size="sm">
          <b-form-input
            id="filter-input"
            v-model="filter"
            type="search"
            placeholder="Nhập từ khóa để tìm kiếm"
          ></b-form-input>
          <b-input-group-append>
            <b-button :disabled="!filter" @click="filter = ''">Xóa</b-button>
          </b-input-group-append>
        </b-input-group>
      </b-form-group>
      <b-table
        striped
        hover
        :items="danh_sach_hoa_don"
        :fields="fields_table"
        :filter="filter"
        :per-page="perPage"
        :current-page="currentPage"
        id="my-table"
        class="table-hoadon"
      >
        <template #cell(stt)="data">
          {{ data.item.stt }}
        </template>
        <template #cell(nocu)="data">
          {{ formatMoney(data.item.nocu, 0, 2) }} VNĐ
        </template>
        <template #cell(dathanhtoan)="data">
          {{ formatMoney(data.item.dathanhtoan, 0, 2) }} VNĐ
        </template>
        <template #cell(tongtien)="data">
          {{  formatMoney(sum_arr(data.item.cthd.map(item => parseFloat(item.thanhtien))), 0, 2) }} VNĐ
        </template>
        <template #cell(key)="data">
          <button
            @click.prevent="$router.push('/chi-tiet-hoa-don/' + data.item.mahd)"
            class="btn btn-primary"
          >
            Chi tiết
          </button>
          <button
            @click.prevent="
              hoadon_item_selected = JSON.parse(JSON.stringify(data.item));
              show_popup_sua = true;
            "
            class="btn btn-info"
          >
            Sửa
          </button>
          <button
            @click.prevent="deleteHoadon(data.item.key)"
            class="btn btn-danger ml-2"
          >
            Xóa
          </button>
        </template>
      </b-table>
      <b-pagination
        v-model="currentPage"
        :total-rows="danh_sach_hoa_don.length"
        :per-page="perPage"
        aria-controls="my-table"
      ></b-pagination>
    </div>
  </div>
</template>

<script>
import { db } from "../../firebaseDb";
import themmoi from "./themmoi.vue";
import sua from "./sua.vue";

export default {
  data() {
    return {
      show_popup_them_moi: false,
      show_popup_sua: false,
      filter: null,
      perPage: 20,
      currentPage: 1,
      danh_sach_hoa_don: [],

      fields_table: [
        {
          key: "stt",
          label: "STT",
          sortable: true,
        },
        {
          key: "ngaylap",
          label: "Ngày lập",
          sortable: true,
        },
        {
          key: "tenkh",
          label: "Tên KH",
          sortable: true,
        },
        {
          key: "diachi",
          label: "Địa chỉ",
          sortable: true,
        },
        {
          key: "sdt",
          label: "Số điện thoại",
          sortable: true,
        },
        {
          key: "nocu",
          label: "Nợ cũ",
          sortable: true,
        },
        {
          key: "dathanhtoan",
          label: "Đã thanh toán",
          sortable: true,
        },
        {
          key: "tongtien",
          label: "Tổng tiền",
          sortable: true,
        },
        {
          key: "key",
          label: "Hành động",
          sortable: true,
        },
      ],
      hoadon_item_selected: {},
    };
  },
  components: {
    themmoi,
    sua,
  },
  created() {
    db.collection("hoadon").onSnapshot((snapshotChange) => {
      this.danh_sach_hoa_don = [];
      let index = 0;
      snapshotChange.forEach((doc) => {
        let arr_cthd = [];
        db.collection("chitiethoadon")
        .where("mahd", "==", doc.data().mahd+"")
        .onSnapshot((snapshotChange2) => {
          snapshotChange2.forEach((doc2) => {
            arr_cthd.push(doc2.data());
          });
        });

        index++;
        this.danh_sach_hoa_don.push({
          key: doc.id,
          stt: index,
          mahd: doc.data().mahd,
          tenkh: doc.data().tenkh,
          diachi: doc.data().diachi,
          sdt: doc.data().sdt,
          dathanhtoan: doc.data().dathanhtoan,
          ngaylap: doc.data().ngaylap,
          nocu: doc.data().nocu,
          tongtien: doc.data().tongtien,
          cthd: arr_cthd
        });
      });

    });
  },
  methods: {
    deleteHoadon(id) {
      if (window.confirm("Bạn có chắc chắn muốn xóa không?")) {
        db.collection("hoadon")
          .doc(id)
          .delete()
          .then(() => {
            console.log("Đã xóa hóa đơn!");
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
    formatMoney(number, min, max) {
      let num_check = parseFloat(number);
      if (isNaN(num_check)) return number;
      number = parseFloat(number);
      return number.toLocaleString("en", {
        minimumFractionDigits: min,
        maximumFractionDigits: max,
      });
    },
    sum_arr(arr){
      let sum = 0;
      for (const item of arr) {
        sum += item;
      }
      return sum;
    }
  },
};
</script>

<style>
.btn-primary {
  margin-right: 12px;
}
.table-hoadon td {
  white-space: nowrap;
}
</style>
