<template>
  <div class="row justify-content-center">
    <div class="">
      <h3 class="text-center">Cập nhật hóa đơn</h3>
      <form @submit.prevent="capnhat">
        <div class="form-group mt-2">
          <label>Tên khách hàng</label>
          <input
            type="text"
            class="form-control"
            v-model="hoadon.tenkh"
            required
          />
        </div>

        <div class="form-group mt-2">
          <label>Số điện thoại</label>
          <input
            type="text"
            class="form-control"
            v-model="hoadon.sdt"
            required
          />
        </div>

        <div class="form-group mt-2">
          <label>Địa chỉ</label>
          <input
            type="text"
            class="form-control"
            v-model="hoadon.diachi"
            required
          />
        </div>

        <div class="form-group mt-2">
          <label>Ngày</label>
          <b-form-datepicker
            :date-format-options="{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
            }"
            v-model="hoadon.ngaylap"
            locale="en"
          ></b-form-datepicker>
        </div>
        <div class="form-group mt-2">
          <label>Đã thanh toán</label>
          <input
            type="number"
            class="form-control"
            v-model="hoadon.dathanhtoan"
            required
          />
        </div>

        <div class="form-group mt-2">
          <label>Nợ cũ</label>
          <input
            type="number"
            class="form-control"
            v-model="hoadon.nocu"
            required
          />
        </div>
        <div class="form-group mt-2 mt-2">
          <button class="btn btn-success btn-block">Cập nhật</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../../firebaseDb";

export default {
  props: ["hoadon_item"],
  data() {
    return {
      hoadon: {
        id: "",
        mahd: 0,
        tenkh: "",
        diachi: "",
        sdt: "",
        dathanhtoan: 0,
        ngaylap: new Date(new Date().getTime()).toISOString().substr(0, 10),
        nocu: 0,
        tongtien: 0,
      },
    };
  },
  created() {
    this.hoadon = JSON.parse(JSON.stringify(this.hoadon_item));
  },
  methods: {
    capnhat(e) {
      e.preventDefault();
      db.collection("hoadon")
        .doc(this.hoadon.key)
        .update(this.hoadon)
        .then(() => {
          alert("Cập nhật thành công!");
        })
        .catch((error) => {
          alert("Có lỗi xảy ra vui lòng thử lại sau!");
          console.log(error);
        });
    },
  },
};
</script>
